import React from "react";
import {
  Page,
  Text,
  View,
  Document,
  StyleSheet,
} from "@react-pdf/renderer";
import { relative } from "path";

const styles = StyleSheet.create({
  table: {
    borderStyle: 'solid',
    borderWidth: 2,
    width: 599,
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'wrap'
  },
  tableRow: {
    position: 'relative',
    display: 'flex',
    width: 594,
    flexDirection: 'row',
    borderStyle: 'solid',
    borderWidth: 1,
    borderBottomWidth: 0
  },
  tableCol1: {
    borderStyle: 'solid',
    borderWidth: 1,
    width: 198,
    position: 'relative',
    borderLeftWidth: 0,
    borderTopWidth: 0,
  },
  tableCol3: {
    width: 593,
    height: '100%',
    display: 'flex',
    flexDirection: 'row',
    borderStyle: 'solid',
    borderWidth: 1,
    borderLeftWidth: 0,
    borderTopWidth: 0,
    borderBottomWidth: 0
  },
  colTop: {
    position: 'absolute',
    display: 'flex',
    flexDirection: 'row',
    height: '50%',
    width: '100%',
    top: 0,
    right: 0,
    paddingLeft: 20,
    paddingRight: 20,
  },
  colBottom: {
    position: 'absolute',
    height: '50%',
    bottom: 0,
    width: '100%',
    marginLeft: 'auto',
    marginRight: 'auto',
    right: 0,
    paddingLeft: 10,
    paddingRight: 10,
  },
  textAlignR: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    paddingTop: 10,
    fontSize: 14,
    height: '100%',
    width: '35%',
    textAlign: 'right'
  },
  textAlignC: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    paddingTop: 10,
    fontSize: 14,
    height: '100%',
    width: '30%',
    textAlign: 'center'
  },
  textAlignL: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    paddingTop: 10,
    fontSize: 14,
    height: '100%',
    width: '35%',
    textAlign: 'left'
  },
  textAddress: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    paddingTop: 10,
    paddingLeft: 15,
    paddingRight: 15,
    fontSize: 12,
    width: '100%',
    height: '100%',
    textAlign: 'center'
  },
});

export function ClientBriefDocument(props) {
  const { printSetting, data } = props;
  return (
    <Document>
      <Page style={{ 'margin': '0 auto', 'size': 'A4' }}>
        <View style={styles.table}>
          {data.map((item, key) => {
            return(
                <View key={key} style={{...styles.tableCol1, height: printSetting.format === '35' ? 92 : 67}}>
                  <View style={styles.colTop}>
                    <Text style={styles.textAlignR}>
                      {printSetting.title === 'blank' 
                        ? '' 
                        : item.title
                      }
                    </Text>
                    <Text style={styles.textAlignC}>
                      {printSetting.name === 'nric' 
                        ? item.nricName 
                        : item.preferredName
                      }
                    </Text>
                    <Text style={styles.textAlignL}>
                      {printSetting.suffix === 'blank' 
                        ? ''
                        : printSetting.suffix === 'family' 
                          ? '& Family'
                          : item.family 
                            ? '& Family'
                            : ''
                      }
                    </Text>
                  </View>
                  <View style={styles.colBottom}>
                    <Text style={styles.textAddress}>
                      {printSetting.address === 'address'
                        ? item.address
                        : item.companyaddress
                      }
                    </Text>
                  </View>
                </View>
            )  
          })}
        </View>
      </Page>
    </Document>
  );
}
